import { headerjson as en } from "@/constants/home";
import { headerjson as cn } from "@/constants/home_cn";
export default {
  name: "myheader",
  data() {
    return {};
  },
  methods: {
    dropdownHandler(str: string) {
      if (str === "cn") {
        this.$store.state.isUs = false;
      } else {
        this.$store.state.isUs = true;
      }
    }
  },
  computed: {
    isUs(): boolean {
      return this.$store.state.isUs;
    },
    headerjson(): Array<object> {
      return this.$store.state.isUs ? en : cn;
    }
  }
};
